@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,600;0,800;1,200&display=swap');
:root{
  --fg: #202d36;
  --bg1:rgb(238, 237, 237);
  --bg2:rgba(255, 255, 255, 0.774);
  --dull-fg:#363535;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

body {
  font-family: 'Nunito Sans', sans-serif;
  background-color: var(--bg1);
  color: #fff;
  transition: color 0.5s,background 1s;
}
.dark{
  --fg:white;
  --bg1:#202d36;
  --bg2:#2b3743;
  --dull-fg:#ffffff97;
}

.header {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--bg2);
  padding: 0 100px;
  color: var(--fg);
  font-size: .8em;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
}

header #switcher {
  cursor: pointer;
}

.filter {
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter .region-filter .select {
  background-color: var(--bg2);
  color: var(--fg);
  font-family: inherit;
  padding: 8px;
  border: none;
  width: 200px;
  border-radius: 5px;
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
}

@media (max-width:620px) {
  .header {
    padding: 0 30px;
    font-size: .7em;
  }
  .filter {
    flex-direction: column;
    width: 100%;
  }
  .filter form {
    width: 100%;
    margin-bottom: 20px;
  }
  .filter .region-filter {
    width: 100%;
  }
  .filter, .grid {
    padding: 30px 30px;
  }
}

.filter form input {
  background: var(--bg2);
  border: none;
  outline: none;
  border-radius: 5px;
  color: var(--fg);
  padding: 8px;
  font-family: inherit;
  letter-spacing: .5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.filter form input::placeholder {
  color: var(--fg);
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
}

@media (min-width:620px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .filter, .grid {
    padding: 30px 50px;
  }
}

@media (min-width:768px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .filter, .grid {
    padding: 30px 80px;
  }
}

@media (min-width:962px) {
  .filter, .grid {
    padding: 30px 100px;
  }
  .grid {
    grid-template-columns: repeat(4, 1fr);
    gap: 60px;
  }
}

.grid article {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  background: var(--bg2);
  border-radius: 5px;
  color: var(--fg);
  text-decoration: none;
  text-shadow: none;
}

.grid article img {
  width: 100%;
  object-fit: cover;
  border-radius: 5px 5px 0 0;
}

.grid .details {
  background: var(--bg2);
  padding: 20px;
  border-radius: 0 0 5px 5px;
}

.flexbox article {
  color: var(--fg);
  text-decoration: none;
  text-shadow: none;
}

.flexbox {
  padding: 30px 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.flexbox button {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  margin: 30px 0;
  background: var(--bg2);
  border: none;
  outline: none;
  border-radius: 5px;
  color: var(--fg);
  padding: 8px;
  font-family: inherit;
  letter-spacing: .5px;
  cursor: pointer;
}

.flexbox article {
  width: 100%;
  display: flex;
  margin: 10px 0;
}
.flexbox article img{
  width: 550px;
  height: 380px;
  object-fit: cover;
}

.flexbox article .details {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.flexbox article .details span {
  color: var(--dull-fg);
  font-weight: 200;
}

.flexbox article .details .title-div {
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.flexbox article .details .title-div h2 {
  font-size: 2.5em;
  margin-top: -.5em;
  margin-bottom: .1em;
}

.flexbox article .details .info-div {
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}


@media (max-width:1162px) {
  .flexbox {
    padding: 30px 70px;
  }
  .flexbox article img{
    width: 50%;
    height: auto;
    object-fit: cover;
  }
}

@media (max-width:1068px) {
  .flexbox {
    padding: 30px 40px;
  }
}

@media (max-width:900px) {
  .flexbox {
    padding: 30px 25px;
  }
  .flexbox article img{
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  .flexbox article {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
  }
  .flexbox article .details {
    padding: 30px 0;
    width: unset;
    height: unset;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
  }
  .flexbox article .details .title-div {
    height: unset;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 30px 0;
  }
  .flexbox article .details .info-div {
    height: unset;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
}
